import React, { Component } from 'react'
import './index.css'
import SimpleReactLightbox from 'simple-react-lightbox'


export default class index extends Component {

  render() {
    return (
      /* Portfolio */
      <div id="portfolio">
        {/* 讓p=0，才不會讓容器的最兩邊會有空隙 */}
        <div className="container-fluid p-0">
          {/* 讓gutter=0 兩張照片的中間才不會有空隙 */}
          <div className="row g-0">
            <div className="col-lg-4 col-sm-6">
              <a className="portfolio-box" href="../../img/portfolio/fullsize/1.jpg" title="Project Name">
                <img className="img-fluid" src="../../img/portfolio/thumbnails/1.jpg" alt="..." />
                <div className="portfolio-box-caption">
                  <div className="project-category text-white-50">Category</div>
                  <div className="project-name">Project Name</div>
                </div>
              </a>
            </div>
            <div className="col-lg-4 col-sm-6">
              <a className="portfolio-box" href="../../img/portfolio/fullsize/2.jpg" title="Project Name">
                <img className="img-fluid" src="../../img/portfolio/thumbnails/2.jpg" alt="..." />
                <div className="portfolio-box-caption">
                  <div className="project-category text-white-50">Category</div>
                  <div className="project-name">Project Name</div>
                </div>
              </a>
            </div>
            <div className="col-lg-4 col-sm-6">
              <a className="portfolio-box" href="../../img/portfolio/fullsize/3.jpg" title="Project Name">
                <img className="img-fluid" src="../../img/portfolio/thumbnails/3.jpg" alt="..." />
                <div className="portfolio-box-caption">
                  <div className="project-category text-white-50">Category</div>
                  <div className="project-name">Project Name</div>
                </div>
              </a>
            </div>
            <div className="col-lg-4 col-sm-6">
              <a className="portfolio-box" href="../../img/portfolio/fullsize/4.jpg" title="Project Name">
                <img className="img-fluid" src="../../img/portfolio/thumbnails/4.jpg" alt="..." />
                <div className="portfolio-box-caption">
                  <div className="project-category text-white-50">Category</div>
                  <div className="project-name">Project Name</div>
                </div>
              </a>
            </div>
            <div className="col-lg-4 col-sm-6">
              <a className="portfolio-box" href="../../img/portfolio/fullsize/5.jpg" title="Project Name">
                <img className="img-fluid" src="../../img/portfolio/thumbnails/5.jpg" alt="..." />
                <div className="portfolio-box-caption">
                  <div className="project-category text-white-50">Category</div>
                  <div className="project-name">Project Name</div>
                </div>
              </a>
            </div>
            <div className="col-lg-4 col-sm-6">
              <a className="portfolio-box" href="../../img/portfolio/fullsize/6.jpg" title="Project Name">
                <img className="img-fluid" src="../../img/portfolio/thumbnails/6.jpg" alt="..." />
                <div className="portfolio-box-caption p-3">
                  <div className="project-category text-white-50">Category</div>
                  <div className="project-name">Project Name</div>
                </div>
              </a>
            </div>
          </div>
        </div>
      </div>
    )
  }
}

