import React, { Component } from 'react'
import './index.css'

export default class index extends Component {
  render() {
    return (
      /* Masthead */
      <header className="masthead">
        <div className="container px-4 px-lg-5 h-100">
          <div className="row gx-4 gx-lg-5 h-100 align-items-center justify-content-center text-center">

            <div className="col-lg-10 align-self-end">
              <h1 className="text-white font-weight-bold">Hello! I am Wang Yen Hao <br /> You can also call me Kola</h1>
              <hr className="divider" />
            </div>

            <div className="col-lg-8 align-self-baseline">
              <p className="text-white-75 mb-5">All the failures, and lose your failure, are negligible.</p>
              <a className="btn btn-primary btn-xl" href="#about">Find Out More</a>
            </div>

          </div>
        </div>
      </header>
    )
  }
}
