import React, { Component } from 'react'
import './index.css'

export default class index extends Component {
  render() {
    return (
    /* Service */
        <section className="page-section" id="services">
          <div className="container px-4 px-lg-5">
            <h2 className="text-center mt-0">At Your Service</h2>
            <hr className="divider" />
            <div className="row gx-4 gx-lg-5">
              <div className="col-lg-4 col-md-12 text-center px-sm-5 px-lg-3">
                <div className="mt-5">
                  <div className="mb-2"><i className="bi-heart fs-1 text-primary" /></div>
                  <h3 className="h4 mb-2">Curiosity</h3>
                  <p className="text-muted mb-0">Not only CS, I am curious about other fields,
                    like stock, photography and japanese. If there is any thing I am interested, I will give it a try.
                  </p>
                </div>
              </div>
              <div className="col-lg-4 col-md-12 text-center px-sm-5 px-lg-3">
                <div className="mt-5">
                  <div className="mb-2"><i className="bi-gem fs-1 text-primary" /></div>
                  <h3 className="h4 mb-2">Actionable</h3>
                  <p className="text-muted mb-0">I am also a actionable person. As stated above, Curiosity is one of my personality,
                    so I join stock race and photography competition when I am freshman. There are still lots of competition in
                    the future and I will join all of them if that is what I am interested in.
                  </p>
                </div>
              </div>
              <div className="col-lg-4 col-md-12 text-center px-sm-5 px-lg-3">
                <div className="mt-5">
                  <div className="mb-2"><i className="bi-globe fs-1 text-primary" /></div>
                  <h3 className="h4 mb-2">Ambitious</h3>
                  <p className="text-muted mb-0">Unwilling to be ordinary people. I want to go into the world, seeing various
                    great people, learning from them to become a great people like them.
                  </p></div>
              </div> 
            </div>
          </div>
        </section>
    )
  }
}
