import React, { Component } from 'react'
import './index.css'

export default class index extends Component {
  render() {
    return (
      /* About */
      <section className="page-section bg-primary" id="about">
        <div className="container px-1 px-lg-5">
          <div className="row gx-4 gx-lg-5 align-items-center">
            <div className="col-lg-5 text-center mb-lg-0 mb-sm-5 ">
              <img src="../img/avaster-2.png" className="img-fluid " alt="avaster" />
            </div>
            <div className="col-lg-7 text-center ">
              <h2 className="text-white mt-0">A Sophomore which study in <br /> Taiwan Chung Cheng University</h2>
              <hr className="divider divider-light" />
              <p className="text-white-75 mb-4">A man who is study in Computer Science now. Currently advance 
                the skill of web development, android development and algorithm.   
              </p>
              <a className="btn btn-light btn-xl" href="#services">Get Started!</a>
            </div>
          </div>
        </div>
      </section>
    )
  }
}
